<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
import StyleEditor from "./components/StyleEditor";
import ResumeEditor from "./components/ResumeEditor";
import "./assets/reset.css";
export default {
  name: "app",
  components: {
    StyleEditor,
    ResumeEditor
  },
  data() {
    return {
      interval: 3,
      currentStyle: "",
      enableHtml: false,
      fullStyle: [
        `/*
* Inspired by http://strml.net/
* 
* 
* 
*/

/*  */
* {
  transition: all .1s;
}

/*  */
html {
  color: rgb(222,222,222);
  background: rgb(0,43,54);
}

/*  */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 90vw;
  margin: 2.5vh 5vw;
  height: 90vh;
}

/*  */
.styleEditor {
  height: 45vh;
}

/*  */
.token.selector{
  color: rgb(133,153,0);
}
.token.property{
  color: rgb(187,137,0);
}
.token.punctuation{
  color: yellow;
}
.token.function{
  color: rgb(42,161,152);
}

/* 3D */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  transform: rotateX(-10deg) translateZ(-50px) ;
}

/*  */
.resumeEditor{
  position: fixed;
  top: 50%; left: 0;
  padding: .5em;  margin: 2.5vh;
  width: 95vw; height: 45vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}

/*  */
`,
        `
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`
      ],
      currentMarkdown: "",
      fullMarkdown: `

----

Python


----

* Python 
* Ruby 
* PHP 
* Node.js


----

1. 
2. 
3. ...


----
1.Taidii 

+ ****pycharmLinux
+ ****PythonDjangoMysqlRedisJavaScriptAjaxHTMLJSONVUE
+ ****
      webAPPDjangorest-frameworkMySQLRedis
      HTML+JavaScript+css
      Ajax+JSON 
+ ****
  &emsp;  1
  &emsp;  2
  &emsp;  3
  &emsp;  4
  &emsp;  5
  &emsp;  6bug

2.Dialog

+ ****pycharmWindows
+ ****PythonMongoDBXMLxml.etree.ElementTree+lxml
+ ****
      xmlxmldomxml
      +
      type
+ ****xmlNode

3.STA+CMS

+ ****RubyMineWindows
+ ****rubysqliteJavaScriptHTML5CSSBootstrapWLAN802.11802.1x
+ ****
      WLANPCAPAP
      openwap2dhcpdot1xsta
       APAPACsta
      
      macmac
      /maccontent
      websocketWiFi
      CMS
      
+ ****
  &emsp;  1ssid
  &emsp;  2dot1x
  &emsp;  3wap2
  &emsp;  4openDHCP
  &emsp;  5
  &emsp;  6open
  &emsp;  7
  &emsp;  8CMS
  &emsp;  9wpa2DHCP
  &emsp;  10

4.PNF

+ ****pycharmWindowsnotepad++
+ ****pythonXMLYAMLINI
+ ****
      pythonPNFdeploy
      envXMLYAMLconfig.ini
      
+ ****
  &emsp;  1
  &emsp;  2
  &emsp;  3
  &emsp;  4sswm+
  &emsp;  5tarGUI;
  &emsp;  6;
  &emsp;  7Linux


----
1.****

  - 
  - 

2.****

  +  python+web
  + python + tornado + web
  + nodejs + 
    &emsp;  1[Hexo + yilia](https://github.com/YaoJusheng/hexo-theme-yilia-plus) 
    &emsp;  2[Hexo + next](https://github.com/YaoJusheng/hexo-theme-next) 
    &emsp;  3[Hexo + diaspora](https://github.com/YaoJusheng/hexo-theme-diaspora) 
    &emsp;  4[Hexo + landscape](https://github.com/YaoJusheng/hexo-theme-landscape) 
    &emsp;  5[Hexo + icarus](https://github.com/YaoJusheng/hexo-theme-icarus) 

3.****

  - CMSPHP + laravel
  - ...


----

* [GitHub](https://github.com/yaojusheng)
* [CSDN](https://blog.csdn.net/weixin_41599858)
* [1](https://blog.yaogro.cn)
* [2](https://yaojusheng.gitee.io/blog/)

> Fork [](https://github.com/yaojusheng/animating-resume)

`
    };
  },
  created() {
    this.makeResume();
  },
  methods: {
    makeResume: async function() {
      await this.progressivelyShowStyle(0);
      await this.progressivelyShowResume();
      await this.progressivelyShowStyle(1);
      await this.showHtml();
      await this.progressivelyShowStyle(2);
    },
    showHtml: function() {
      return new Promise((resolve, reject) => {
        this.enableHtml = true;
        this.$nextTick(() => {
          this.$refs.resumeEditor.goTop();
        });
        resolve();
      });
    },
    progressivelyShowStyle(n) {
      return new Promise((resolve, reject) => {
        let interval = this.interval;
        let showStyle = async function() {
          let style = this.fullStyle[n];
          if (!style) {
            return;
          }
          //  n  style 
          let length = this.fullStyle
            .filter((_, index) => index <= n)
            .map(item => item.length)
            .reduce((p, c) => p + c, 0);
          let prefixLength = length - style.length;
          if (this.currentStyle.length < length) {
            let l = this.currentStyle.length - prefixLength;
            let char = style.substring(l, l + 1) || " ";
            this.currentStyle += char;
            if (style.substring(l - 1, l) === "\n" && this.$refs.styleEditor) {
              this.$nextTick(() => {
                this.$refs.styleEditor.goBottom();
              });
            }
            setTimeout(showStyle, interval);
          } else {
            resolve();
          }
        }.bind(this);
        showStyle();
      });
    },
    progressivelyShowResume() {
      return new Promise((resolve, reject) => {
        let length = this.fullMarkdown.length;
        let interval = this.interval;
        let showResume = () => {
          if (this.currentMarkdown.length < length) {
            this.currentMarkdown = this.fullMarkdown.substring(
              0,
              this.currentMarkdown.length + 1
            );
            let lastChar = this.currentMarkdown[
              this.currentMarkdown.length - 1
            ];
            let prevChar = this.currentMarkdown[
              this.currentMarkdown.length - 2
            ];
            if (prevChar === "\n" && this.$refs.resumeEditor) {
              this.$nextTick(() => this.$refs.resumeEditor.goBottom());
            }
            setTimeout(showResume, interval);
          } else {
            resolve();
          }
        };
        showResume();
      });
    }
  }
};
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  position: relative;
}
html {
  min-height: 100vh;
}
* {
  box-sizing: border-box;
}
</style>


